.dishful {
	&__inner {
		position: relative; }
	.icon {
		color: $brown; }
	.menu-slider__text {
		max-width: none;
		padding: 0 50px;
		text-align: center; }
	.menu__next-arrow {
		right: 71px; } }

.choice {
	&__header {
		background-color: $powder; }
	&__current {
		display: none; }
	&__list {
		display: flex;
		justify-content: center;
		align-items: center; }
	&__title {
		width: 33.3%;
		text-align: center;
		padding: 13px 20px;
		cursor: pointer;
		transition: background-color .3s; }
	&__body {
		[data-tabs="home"] & {
			background-color: $powderDark; }
		[data-tabs="chef"] & {
			background-color: $pinkLight; }
		[data-tabs="light"] & {
			background-color: $greenLight; } }
	&__info {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 45px;
		padding-bottom: 25px; }
	&__select {
		display: flex;
		padding-bottom: 30px; }
	&__basket {
		color: $brown; }
	&__dishes {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px; }
	&__item {
		width: 33.3%;
		padding: 0 10px 40px 10px;
		position: relative; }
	&__link {
		display: block;
		overflow: hidden;
		position: relative;
		@include hover {
			&:before {
				opacity: 1; } }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($black, .2);
			z-index: 1;
			opacity: 0;
			transition: opacity .3s; } } }


.main-dish {
	background-color: $white;
	position: relative;
	&__img {
		width: 100%;
		height: 0;
		padding-bottom: 74.5%;
		@include cover; }
	&__title {
		padding: 40px 30px;
		&.title {
			color: $black; } }
	&__specification {
		display: flex;
		justify-content: space-between;
		padding: 20px; }
	&__check {
		padding-top: 68%;
		top: 0;
		right: 36px;
		z-index: 2;
		position: absolute; } }



.check {
	@include circle(73);
	display: inline-block;
	vertical-align: top;
	position: relative;
	cursor: pointer;
	font-size: 22px;
	user-select: none;
	input {
		position: absolute;
		opacity: 0;
		height: 0;
		width: 0;
		&:checked {
			+ .check__checkmark {} }
		&:checked {
			+ .check__checkmark:after {
				background-color: $green; } } }
	&__checkmark {
		position: absolute;
		top: 0;
		left: 0;
		@include circle(73);
		background-color: $white;
		border: 1px solid $brown;
		&:after {
			content: "";
			position: absolute;
			@include vcenter;
			@include circle(52);
			background-color: $grayMid;
			transition: background-color .2s; } } }


